
import React, { useEffect, useState } from 'react';
import {
    Row,
    Col,
    Card,
    Button,
    Modal,
    FormControl,
    InputGroup
} from "react-bootstrap";
import { Formik } from "formik";
import * as Yup from "yup";

import Aux from "../../../hoc/_Aux";
import axios from 'axios';
var urlbackend = require("../../../env/env")();
const phoneRegExp = /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/;
const SignupSchema = Yup.object().shape({
    email: Yup.string().email("Invalid Email").required("Email is Required"),
    phone_number: Yup.string()
        .matches(phoneRegExp, "Phone number is not valid")
        .required("Phone is Required"),
    city: Yup.string()
        .min(2, "City is Too Short!")
        .max(20, "City is Too Long!")
        .required("City is Required"),
    description: Yup.string()
        .min(2, "description is Too Short!")
        .max(20, "description is Too Long!")
        .required("description is Required"),
    name: Yup.string()
        .min(2, "name is Too Short!")
        .max(20, "name is Too Long!")
        .required("name is Required"),
    code: Yup.string()
        .min(2, "code is Too Short!")
        .max(20, "code is Too Long!")
        .required("code is Required"),
});
function LivraisonDecision(props) {
    const [show, setShow] = useState(false);
    //const [pickupsUpdate] = useState(props.pickup)
    const [drivers, setdriver] = useState([])
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    useEffect(() => {
        const config = {
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Headers': 'Authorization',
                "x-access-token": localStorage.getItem('token'),
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
            },
        };

        axios
            .get(urlbackend + "/driver", config)
            .then((response) => {
                console.log("this respon list", response.data);
                //this.setState({ Drivers: response.data.data });
                setdriver(response.data.data)
            })
            .catch((e) => {
                console.log(e);
            });
    }, [])
    const handlSubmit = (values) => {
       
     if(values.driver){
        const config = {
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Headers': 'Authorization',
                "x-access-token": localStorage.getItem('token'),
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
            },
        };
        axios.post(urlbackend + "/mobile/delivery/add", {driver:values.driver, colis: [props.pickup.colis.id]}, config)
            .then(response => {
                alert("add Succefull")
                console.log(response.data)
                props.change()
                handleClose()
            })
            .catch(e => {
                console.log(e)
            })
     }else{
         alert("choisir un chauffeur")
     }
    };
    const showForm = ({
        values,
        errors,
        touched,
        handleChange,
        handleSubmit,
        handleBlur,
        setFieldValue,
        isSubmitting,
        dirty,
        isValid,
        handleReset,
    }) => {
        // console.log(errors);
        return (
            <Aux>
                <Row>
                    <Col>
                        <Card>
                            <Card.Header>
                                <Card.Title as="h5">Colis dilevred</Card.Title>
                            </Card.Header>
                            <Card.Body>
                                <Row>
                                    <Col md={12}>
                                        <label htmlFor="basic-url">driver</label>

                                        <InputGroup className="mb-3">
                                            <FormControl
                                                as="select"
                                                id="driver"
                                                custom
                                                onChange={handleChange}
                                                onBlur={handleBlur}
                                                value={values.driver}
                                            >
                                                <option>choose driver</option>

                                                {drivers.map((driver, index) => (
                                                    <option key={driver.id} value={driver.id}>
                                                        {driver.username}
                                                    </option>
                                                ))}
                                            </FormControl>
                                        </InputGroup>

                                       {/*  <label htmlFor="basic-url">Type Of Colis</label>

                                        <InputGroup className="mb-3">
                                            <FormControl
                                                as="select"
                                                id="type"
                                                custom
                                                onChange={handleChange}
                                                onBlur={handleBlur}
                                                value={values.type}
                                            >
                                                <option>choose type</option>

                                                {["Docs", "Envelope", "Box"].map((type, index) => (
                                                    <option key={type} value={type}>
                                                        {type}
                                                    </option>
                                                ))}
                                            </FormControl>
                                        </InputGroup>
 */}
                                    </Col>
                                    <Col md={6}>








                                    </Col>
                                </Row>

                                <Button
                                    onClick={() => {
                                        handlSubmit(values);
                                        handleReset();
                                        // console.log(values);
                                    }}
                                >
                                    Save
                </Button>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </Aux>
        );
    };
    return (
        <>
            <Button variant="info"
                onClick={handleShow}> <span className="fa fa-paper-plane"></span></Button>


            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title>Colis dilevred   </Modal.Title>
                </Modal.Header>
                <Modal.Body>


                    <Formik
                        initialValues={{
                        }}
                        onSubmit={(values, { setSubmitting }) => {
                            this.submitForm(values);
                            setSubmitting(false);
                        }}
                        validationSchema={SignupSchema}
                    >
                        {(props) => showForm(props)}
                    </Formik>

                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>
                        Close
            </Button>
                    {/* <Button variant="primary" onClick={handleClose}>
            Save Changes
            </Button> */}
                </Modal.Footer>
            </Modal>
        </>
    );
}

export default LivraisonDecision